<template>
  <div style="min-width: 650px">
    <el-breadcrumb separator="/" class="mb-2">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/">商品添加</a></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="p-1" :body-style="{ padding: '10px' }">
      <div slot="header">
        <span>商品信息填写</span>
      </div>
      <div style="min-width: 650px;">
        <el-form ref="ruleForm" :model="model" label-width="110px" :rules="rules">
          <el-form-item label="商品分类:" prop="categoryId">
            <el-cascader ref="cascaderAddr" v-model="model.categoryId" :options="model.catelist" :props="{ expandTrigger: 'hover' }" size="mini" @change="parentCateChanged"></el-cascader>
          </el-form-item>
          <el-form-item label="名称:" prop="name">
            <el-input
              v-model="model.name"
              class="commAdd"
              size="small"
              placeholder="请输入商品名称"
            />
          </el-form-item>
          <el-form-item label="商品标题:" prop="title">
            <el-input
              v-model="model.title"
              placeholder="请输入商品标题"
              class="commAdd"
              size="small"
            />
          </el-form-item>
          <el-form-item label="商品货号:" prop="code">
            <el-input
              v-model="model.code"
              class="commAdd"
              size="small"
              placeholder="请输入商品货号"
            />
          </el-form-item>
          <!-- <el-form-item label="原价:" prop="originalPrice">
            <el-input
              v-model="model.originalPrice"
              class="commAdd"
              size="small"
              placeholder="请输入原价格"
              @input="onInput"
            />
          </el-form-item> -->
          <el-form-item label="规格:" required>
            <section>
              <div v-for="(item, index) in attributeItem" :key="index" class="spec-line">
                <el-dialog title="库存数量" :visible.sync="dialogValueNum" width="30%" :close-on-click-modal="false">
                  <el-input v-model.number="item.inputValueNum"></el-input>
                  <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogValueNum = false">取 消</el-button>
                    <el-button type="primary" @click="onSave(index)">确 定</el-button>
                  </span>
                </el-dialog>
                <div class="ml-2">
                  <span v-if="!item.status">{{ item.keyName }}</span>
                  <el-input v-if="item.status" v-model="item.inputKeyName" size="small" style="width:200px;" placeholder="输入产品规格">
                    <el-button slot="append" icon="el-icon-check" type="primary" @click="onSaveAttributeKey(index)"></el-button>
                  </el-input>
                  <i v-else class="icon el-icon-edit" @click="onUpdateSpec(item)"></i>
                </div>
                <div class="ml-2">
                  <el-tag v-for="(tag, j) in item.keyValue" :key="j" closable @close="onDelSpecTag(j, index)">属性：{{ tag.valueName }}</el-tag>
                  <el-input v-model="item.inputKeyValue" size="small" style="width:200px;" placeholder="输入产品属性">
                    <el-button slot="append" icon="el-icon-check" type="primary" @click="onAddSpecTag(index)"></el-button>
                  </el-input>
                </div>
                <i class="icon el-icon-circle-close spec-deleted" @click="onDelSpec(index)"></i>
                <el-divider></el-divider>
              </div>
              <div class="add-spec">
                <el-button size="small" type="primary" :disabled="attributeItem.length >= 3" @click="onAddSpec">添加规格项目</el-button>
              </div>
            </section>
          </el-form-item>
          <el-form-item label="属性" required>
            <el-table :data="attributeItemTable" border size="mini">
              <el-table-column v-for="(item, i) in attributeItem" :key="i" :label="item.keyName" align="center">
                <template slot-scope="scope">
                  <span>{{ scope.row.attributeArr[i] }}</span>
                </template>
              </el-table-column>
              <el-table-column label="原价" align="center">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.originalPrice"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="现价" align="center">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.price"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="库存" align="center">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.stock"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="配图" align="center">
                <template slot-scope="scope">
                  <el-upload class="avatar-uploader" action="http-request" :http-request="(params) => onUploadSectionFile(params, scope)" :show-file-list="false">
                    <el-image v-if="scope.row.imgUrl" :src="scope.row.imgUrl" fit="contain" class="avatar" />
                    <i v-else class="el-icon-plus avatar-uploader-icon" />
                  </el-upload>
                </template>
              </el-table-column>
            </el-table>
          </el-form-item>
          <el-form-item label="主图上传：" prop="imageUrl">
            <el-upload
              class="avatar-uploader"
              action="http-request"
              :http-request="uploadSectionFile"
              :show-file-list="false"
            >
              <el-image
                v-if="model.imageUrl"
                :src="model.imageUrl"
                fit="contain"
                class="avatar"
              />
              <i v-else class="el-icon-plus avatar-uploader-icon" />
            </el-upload>
          </el-form-item>
          <el-form-item label="视频上传：" prop="videoUrl">
            <el-upload
              class="avatar-uploader"
              action="http-request"
              :show-file-list="false"
              :http-request="beforeUploadVideo"
              accept=".mp4,.qlv,.qsv,.ogg,.flv,.avi,.wmv,.rmvb"
            >
              <video
                v-if="model.videoUrl"
                :src="model.videoUrl"
                class="avatar"
                controls
              /><i v-else class="el-icon-plus avatar-uploader-icon" />
            </el-upload>
          </el-form-item>
          <el-form-item label="商品详情:" prop="describes">
            <!-- <vue-editor v-model="model.describes" style="width: 750px" use-custom-image-handler @image-added="handleImageAdded"></vue-editor> -->
            <tinymce v-model="model.describes" :height="300" style="min-width: 590px;" />
          </el-form-item>
          <el-form-item label="物流包邮:">
            <el-select v-model="model.logisticsType" placeholder="包邮">
              <el-option
                v-for="item in model.options2"
                :key="item.logistics"
                :label="item.label"
                :value="item.logistics"
              />
            </el-select>
          </el-form-item>
          <el-form-item class="text-center">
            <el-button
              type="submit"
              class="goods"
              @click.native.prevent="handleLogin"
            >提交审核</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>

    <image-compressor-cmp ref="imageCompressorCmp" />

  </div>
</template>

<script>
import Tinymce from '@/components/Tinymce'
import { MIXINS_VALIDATION_NUMBER } from '@/mixins/event.js'
import { MIXINS_DESCRIBES_APPEND_H5 } from './mixins/event.js'
import imageCompressorCmp from '@/components/image_compressor_cmp/index.vue'

export default {
  components: {
    // eslint-disable-next-line vue/no-unused-components
    imageCompressorCmp,
    Tinymce
  },
  data() {
    return {
      // 规格
      model: {
        // 子规格
        item: [],
        categoryName: '',
        videoImageUrl: '',
        picList: [],
        imageName: '', // 图片名称
        imageUrl: '', // 编辑时回显的图片
        advertisingUrl: '',
        videoUrl: '',
        videoName: '',
        logisticsType: 0,
        catelist: [],
        cateProps: {
          expandTrigger: 'hover', // 次级菜单的展开方式 click / hover
          label: 'categoryName',
          value: 'id', // 指定选中值的属性
          children: 'trees',
          checkStrictly: true
        },
        categoryId: [],
        title: '',
        code: '',
        classify: '',
        sort: '',
        state: 0,
        options2: [
          { logistics: 0, label: '包邮' },
          { logistics: 1, label: '按规则计算邮费' },
        ],
        dialogImageUrl: '',
        dialogVisible: false,
        textarea: '',
      },
      rules: {
        name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
        ],
        categoryId: [
          { required: true, message: '请选择分类', trigger: 'blur' },
        ],
        title: [
          { required: true, message: '请填写商品介绍', trigger: 'change' }
        ],
        code: [
          { required: true, message: '请填写商品货号', trigger: 'change' }
        ],
        costPrice: [
          { required: true, message: '请输入售价价格', trigger: 'change' }
        ],
        // originalPrice: [
        //   { required: true, message: '请输入原价格', trigger: 'change' }
        // ],
        imageUrl: [
          { required: true, message: '请添加商品图片', trigger: 'change' }
        ],
        describes: [
          { required: true, message: '请填写商品详情', trigger: 'blur' }
        ],
        inventory: [
          { required: true, message: '请填写商品详情', trigger: 'blur' }
        ]
      },
      attributeItem: [], // 属性
      keyValue: '', // 需要添加的规格
      attributeItemTable: [], // 给规格表格用的数据
      dialogValueNum: false, // 填写库存的弹出框
      saveInventoryIndex: '', // 保存库存时选择的规格是哪一个
      saveInventoryAttributeIndex: '', // 保存库存时选择的属性是哪一个
    }
  },

  mixins: [
    MIXINS_VALIDATION_NUMBER, MIXINS_DESCRIBES_APPEND_H5
  ],

  methods: {
    // // 富文本图片上传
    // async handleImageAdded (file, Editor, cursorLocation, resetUploader) {
    //   const formData = new FormData()
    //   formData.append('file', file)
    //   const res = await this.$http.post('/common/oss/uploadFile', formData)
    //   Editor.insertEmbed(cursorLocation, 'image', res.data.data.webUrl)
    //   resetUploader()
    // },
    // 分类js
    parentCateChanged(val) {
      console.log(val)
      const categoryName = this.$refs['cascaderAddr'].getCheckedNodes()[0].pathLabels
      console.log(this.$refs['cascaderAddr'].getCheckedNodes()[0].pathLabels)
      const category = categoryName.join('/')
      this.model.categoryName = category.split('/')[1]
    },

    // 添加商品
    handleLogin () {
      if (this.attributeItemTable.length === 0) {
        this.$message.error('规格不能为空')
        return false
      }
      let isAttributeItemTable = false
      this.attributeItemTable.some((item) => {
        if (item.price === '' || item.imgUrl === '' || item.stock === '' || item.originalPrice === '') {
          return true
        } else {
          isAttributeItemTable = true
        }
      })
      if (!isAttributeItemTable) {
        this.$message.error('请把规格表格填完')
        return false
      }
      if (this.model.categoryId.length < 2) {
        return this.$notify.error('请选择二级分类')
      }
      let data = JSON.stringify(this.model)
      let dataJson = JSON.parse(data)
      dataJson.categoryId = this.model.categoryId[1]
      this.$refs.ruleForm.validate((valid) => {
        if (!valid) {
          return this.$notify.error('表单填写不完整')
        }
        const loading = this.$loading(this.$optionsCmp)
        dataJson.describes = this._mixinsDescribesAppendH5(dataJson.describes) // 富文本编辑
        dataJson.items = this.attributeItem
        let attributeItemTableReplaceData = this.attributeItemTable.map((item) => {
          return {
            attrSymbolNo: item.attributeArr.join(','),
            price: item.price,
            imgUrl: item.imgUrl,
            stock: item.stock,
            originalPrice: item.originalPrice,
          }
        })
        dataJson.skus = attributeItemTableReplaceData
        dataJson.type = 0
        console.log('dataJson', dataJson)
        loading.close()
        // return false
        this.$http.post(this.$api.productInsert, dataJson).then(res => {
          loading.close()
          if (res.data.success) {
            this.$message.success('提交成功')
            this.$router.push('/commo/list')
          } else {
            this.$message.error(res.data.msg)
          }
        })
      })
    },
    // 获取分类数据
    async getCateList() {
      const res = await this.$http.get(`/woho/product/category/findByTree/${localStorage.getItem('userArea')}`)
      this.model.catelist = this.getTreeData(res.data.data)
    },
    getTreeData(data) {
      // eslint-disable-next-line no-undef
      for (var i = 0; i < data.length; i++) {
        // eslint-disable-next-line no-undef
        data[i].label = data[i].categoryName
        data[i].value = data[i].id
        if (data[i].trees.length < 1) {
          // children若为空数组，则将children设为undefined
          // eslint-disable-next-line no-undef
          data[i].children = undefined
        } else {
          // children若不为空数组，则继续 递归调用 本方法
          // eslint-disable-next-line no-undef
          data[i].children = this.getTreeData(data[i].trees)
        }
      }
      // eslint-disable-next-line no-undef
      return data
    },

    // // input不可输入数字和小数点以外的
    // onInput (e) {
    //   this._validationNumber(e)
    //   this.model.originalPrice = e.replace(/[^0-9.]/g, '')
    // },
    // 点击保存规格名字
    onSaveAttributeKey (index) {
      if (!this._validAttributeKey(this.attributeItem[index].inputKeyName, this.attributeItem)) {
        return false
      }
      this.attributeItem[index].keyName = this.attributeItem[index].inputKeyName
      this.attributeItem[index].status = false
    },
    // 点击确定保存规格
    onSave (index) {
      // if (!this.attributeItem[index].inputValueNum && this.attributeItem[index].inputValueNum !== 0) {
      //   this.$message.error('库存不能为空')
      //   return false
      // }
      let data = {
        valueName: this.attributeItem[this.saveInventoryIndex].inputKeyValue,
        // valueNum: this.attributeItem[index].inputValueNum // 库存
      }
      console.log('this.saveInventoryAttributeIndex', this.saveInventoryAttributeIndex)
      // if (this.saveInventoryAttributeIndex === '') {
      this.attributeItem[this.saveInventoryIndex].keyValue.push(data)
      // } 
      // else {
      //   this.attributeItem[this.saveInventoryIndex].keyValue[this.saveInventoryAttributeIndex].valueNum = this.attributeItem[index].inputValueNum
      // }
      this.attributeItem[this.saveInventoryIndex].inputKeyValue = ''
      this._attributeItemTable()
      // this.dialogValueNum = false
      this.saveInventoryAttributeIndex = ''
    },
    // 编辑规格名
    onUpdateSpec (item) {
      item.status = true
      item.keyName = ''
    },
    // 删除一个属性
    onDelSpecTag (j, index) {
      this.attributeItem[index].keyValue.splice(j, 1)
      this._attributeItemTable()
    },
    // 删除一个规格
    onDelSpec (index) {
      this.attributeItem.splice(index, 1)
      this._attributeItemTable()
    },
  },

  created() {
    this.getCateList()
  }
}
</script>

<style lang="scss" scoped>
  @import '../../assets/css/goods';
  .el-icon-plus{
    border: 1px dashed #ccc;
  }
  .el-icon-edit{
    cursor: pointer
  }
</style>
